import React, {Component} from 'react';

import All1 from '../foto/Semua/1.jpg'
import All2 from '../foto/Semua/2.jpg'
import All3 from '../foto/Semua/3.jpg'
import All4 from '../foto/Semua/4.jpg'

import Moto1 from '../foto/Moto-Moto/1.jpg';
import Moto2 from '../foto/Moto-Moto/2.jpg';
import Moto3 from '../foto/Moto-Moto/3.jpg';
import Moto4 from '../foto/Moto-Moto/4.jpg';
import Moto5 from '../foto/Moto-Moto/5.jpg';
import Moto6 from '../foto/Moto-Moto/6.jpg';
import Moto7 from '../foto/Moto-Moto/7.jpg';
import Moto8 from '../foto/Moto-Moto/8.jpg';
import Moto9 from '../foto/Moto-Moto/9.jpg';
import Moto10 from '../foto/Moto-Moto/10.jpg';

export default class Foto extends Component{
    render(){
        return(
            <div className='fotoBgContainer'>
                <div className='fotoContainer'>
                    <div className='All'>
                        <p>All</p>
                        <div className='allContainer'>
                            <img src={All1} alt='All1'></img>
                            <img src={All2} alt='All2'></img>
                            <img src={All3} alt='All3'></img>
                            <img src={All4} alt='All4'></img>
                        </div>
                    </div>

                    <div className='Uprak'>
                        <p>Uprak</p>
                    </div>

                    <div className='Moto-Moto'>
                        <p>Moto-Moto</p>
                        <div className='motoContainer'> 
                            <img src={Moto1}alt='Moto1'></img>
                            <img src={Moto2}alt='Moto2'></img>
                            <img src={Moto3}alt='Moto3'></img>
                            <img src={Moto4}alt='Moto4'></img>
                            <img src={Moto5}alt='Moto5'></img>
                            <img src={Moto6}alt='Moto6'></img>
                            <img src={Moto7}alt='Moto7'></img>
                            <img src={Moto8}alt='Moto8'></img>
                            <img src={Moto9}alt='Moto9'></img>
                            <img src={Moto10}alt='Moto10'></img>
                        </div>
                    </div>

                    <div className='Random'>
                        <p>Random</p>
                   </div>
                   
                </div>
            </div>
        )
    }
}