import React, {Component} from 'react';
import {Link} from 'react-router-dom';

export default class Buttons extends Component{
    render(){
        return(
            <div className='btns'>
                <Link className='home' to='/mia'>Home</Link>
                <Link className='walibtn' to='/mia/WaliKelas'>Wali Kelas</Link>
                <Link className='Photos' to='/mia/foto'>Foto</Link>
            </div>
        )
    }
}