import React, {Component} from 'react';

import Pic from '../images/Juanda.jpg';

export default class Wali extends Component{
    render(){
        return(
            <div className='Container'>
                <div className='wali'>
                    <img src={Pic} alt='wali' className='picwali'/>
                    <div className='descContainer'>
                        <p className='bpk'>Bapak</p>
                        <p className='namaWali'> Juanda Naiborhu</p>
                        <p className='descWali'>Wali Kelas | Guru Kimia</p>
                    </div>
                </div>
            </div>
        )
    }
}